<template>
  <div>
    <a-card title="ozon订单管理">
      <a-row :gutter="16">
        <a-col :span="24" style="max-width: 200px; margin-bottom: 12px;">
          <a-input v-model="searchForm.keyWord" placeholder="关键字查询.." allowClear @pressEnter="search" />
        </a-col>
        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table size="small" :columns="columns" :dataSource="items" rowKey="id" :loading="loading" :pagination="pagination" @change="tableChange">
          <div slot="is_active" slot-scope="value">
            <a-tag :color="value ? 'green' : 'red'">{{value ? '激活' : '冻结'}}</a-tag>
          </div>
          <div slot="products" slot-scope="text, record">
            <div v-for=" product,index in record.products" :key="index+100">
              <span> {{ index+1 }}. </span>
              <span> offer_id: </span> <span style="color: red;"> {{ product.offer_id }} </span> 
              <span> 价格: </span> <span style="color: green;">{{ product.price }}</span> 
              <span> 数量: </span> <span style="color: skyblue;">{{ product.quantity }}</span>
              <hr>
            </div>
          </div>
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openFormModal(item)">查看</a-button>
            </a-button-group>
          </div>
        </a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :form="targetItem" @update="update" />
  </div>
</template>

<script>
import moment from 'moment'
import {getOzonOrderData } from '@/api/operation'

export default {
  name: 'OzonOrder',
  components: {
    FormModal: () => import('./FormModal.vue'),
  },
  data() {
    return {
      columns: [
        {
          title: '订单ID',
          dataIndex: 'order_id',
        },
        {
          title: '货件号',
          dataIndex: 'posting_number',
          key: 'posting_number',
          width: '145px'
        },
        {
          title: '国际运单号',
          dataIndex: 'tracking_number',
          key: 'tracking_number',
          width: '145px'
        },
        {
          title: '货运状态',
          dataIndex: 'posting_status',
        },
        {
          title: '物流状态',
          dataIndex: 'logistics_status',
        },
        {
          title: '订单总价',
          dataIndex: 'total_price',
        },
        {
          title: '系统状态',
          dataIndex: 'system_status',
        },
        {
          title: '开始处理时间',
          dataIndex: 'in_process_at',
          customRender: (value, item, index) => {
              return moment(value).format('YYYY-MM-DD HH:mm:ss')
            },
        },
        {
          title: '必须收取时间',
          dataIndex: 'shipment_date',
          customRender: (value, item, index) => {
            return moment(value).format('YYYY-MM-DD HH:mm:ss')
          } 
        },
        {
          title: '产品',
          dataIndex: 'products',
          scopedSlots: { customRender: 'products' },
        },
        {
          title: '负责人',
          dataIndex: 'owner',
          customRender: (value, item, index) => {
            return value?value.name:"店铺未绑定"
          } 
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
          width: '156px'
        },
      ],
      searchForm: { start: 0, limit: 15, keyWord: "", system_status:"" },
      pagination: { current: 1, total: 0, pageSize: 15 },
      loading: false,
      items: [],

      visible: false,
      targetItem: {},
      form: {},
      importLoading: false,
    };
  },
  computed: {
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getOzonOrderData(this.searchForm).then(data => {
        this.pagination.total = data.data.count;
        this.items = data.data.data;
      }).finally(() => {
        this.loading = false;
      });
    },
    update() {
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    openFormModal(item) {
      this.targetItem = { ...item };
      this.visible = true;
    },
    tableChange(pagination, filters, sorter) {      
      this.searchForm.start = (pagination.current-1)*this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
  },
  mounted() {
    this.initialize();
  },
}
</script>